import React from 'react';
import {S} from "./WorkTabs_Styles";

export type TabStatusType = "all" | "sites" | "react"

type WorkTabsPropsType = {
    tabsItems: Array<{ status: TabStatusType, title: string }>
    changeFilterStatus: (value:TabStatusType ) => void
    currentFilterStatus: string
 }

export const WorkTabs = (props: WorkTabsPropsType) => {
    return (
        <S.StyledWorkTabs>
            <ul>
                {props.tabsItems.map((tabItem, index) => {
                    return (
                        <li key={index}>
                            <S.TabsButton active={props.currentFilterStatus === tabItem.status} onClick={() => {props.changeFilterStatus(tabItem.status)}}>{tabItem.title}</S.TabsButton>
                        </li>
                    )
                })}
            </ul>
        </S.StyledWorkTabs>
    );
};

