import React from 'react';
import {S} from "./Work_Styles";
import {Icon} from "../../../../components/icon/Icon";
import { motion } from 'framer-motion';


type WorkPropsType = {
    src: any,
    title: string,
    text: string,
    hrefPreview: string,
    hrefCode: string,
}
export const Work = (props: WorkPropsType) => {
    return (
        <S.StyledWork as={motion.div}
                      initial={{opacity: 0}}
                      animate={{opacity: 1}}
                      exit={{opacity: 0}}
                      layout>
            <S.WorkImg src={props.src}/>
            <S.WorkInnerBox>
                <S.WorkTitle>{props.title}</S.WorkTitle>
                <S.WorkText>{props.text}</S.WorkText>
                <S.WorkLinkBox>
                    <S.WorkLink href={props.hrefPreview}><Icon iconId={'chain'} width={"20"} height={"20"}
                                      viewBox={"0 0 25 25"}/> Preview</S.WorkLink>
                    <S.WorkLink href={props.hrefCode}><Icon iconId={'github'} width={"20"} height={"20"}
                                      viewBox={"0 0 120 120"}/> Code</S.WorkLink>
                </S.WorkLinkBox>
            </S.WorkInnerBox>
        </S.StyledWork>
    );
};

