import React from "react";
import { Icon } from "../../../components/icon/Icon";
import { Title } from "../../../components/title/Title";
import { FlexWrapper } from "../../../components/wrapper/FlexWrapper";
import { Wrapper } from "../../../components/wrapper/Wrapper";
import { S } from "./Stack_Styles";

const IconsParams= [
  {
    iconId: "html",
    delay: "0s",
  },
  {
    iconId: "css",
    delay: ".2s",
  },
  {
    iconId: "js",
    delay: ".4s",
  },
  {
    iconId: "react",
    delay: ".5s",
  },
  {
    iconId: "redux",
    delay: ".6s",
  },
  {
    iconId: "sass",
    delay: ".8s",
  },
  {
    iconId: "git",
    delay: "1s",
  },
  {
    iconId: "greensock",
    delay: "1.2s",
  },
  {
    iconId: "vscode",
    delay: "1.4s",
  },
  {
    iconId: "github",
    delay: "1.6s",
  },
]

export const Stack = () => {
  return(
    <S.StyledStack>
      <Wrapper paddingTop="50px" paddingBottom="30px">
        <Title text="My Tech Stack" />
        <FlexWrapper columnGap="70px" rowGap="70px" justify="center" padding="90px 0" width="900px">
          {IconsParams.map((param, index) => {
            return(
              <S.StackBox delay={param.delay} key={index}>
                <Icon iconId={param.iconId} width="90" height="90" viewBox="0 0 120 120"/>
              </S.StackBox>
            )
          })}
        </FlexWrapper>
      </Wrapper>
    </S.StyledStack>
  )
}