import React from "react";
import Typewriter from 'typewriter-effect';
import { FlexWrapper } from "../../../components/wrapper/FlexWrapper";
import { Wrapper } from "../../../components/wrapper/Wrapper";
import { S } from "./Hero_Styles";

export const Hero = () => {
  return (
    <S.StyledHero>
      <Wrapper>
        <FlexWrapper height="100vh" align="center">
          <S.HeroTextBox>        
              Hi! I'm Ivan, a <Typewriter 
                options={{
                  strings: ['FrontEnd Developer', 'React Developer'],
                  autoStart: true,
                  loop:true,
                  delay: 100,
                  deleteSpeed: 150,
                }}
              />
            <S.HeroText>
              The motto in life: I see a goal - I go to the goal and no matter how far it is.
            </S.HeroText>
          </S.HeroTextBox>
        </FlexWrapper>
      </Wrapper>
    </S.StyledHero>
  )
}

