import React from 'react';
import styled from "styled-components";
import {myTheme} from "../../../../styles/Theme.styled";
import {motion} from "framer-motion";


const StyledWork = styled.div`
  grid-column: 4 span;
  border-radius: 30px;
  background-color: ${myTheme.colors.grey.light};

   @media ${myTheme.media.tablet}{
     grid-column: 6 span
   }

  @media ${myTheme.media.mobile}{
    grid-column: 12 span
  }
`

const WorkImg = styled.img`
  object-fit: cover;
  width: 100%;
  height: 260px;
  display: block;
  border-radius: 20px 20px 0 0;
`

const WorkInnerBox = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
  min-height: 250px;
`

const WorkTitle = styled.span`
  display: block;
  font-size: 22px;
  color: white;
  font-family: ${myTheme.fonts.poppins};
  text-transform: uppercase
`

const WorkText = styled.p`
  font-size: 16px;
  color: white;
  font-family: ${myTheme.fonts.poppins};
  margin: 20px 0;
  flex: 1;
`

const WorkLinkBox = styled.div`
  display: flex;
  column-gap: 40px;
`

const WorkLink = styled.a`
  font-size: 14px;
  font-family: ${myTheme.fonts.poppins};
  color: white;
  display: flex;
  align-items: center;
  
  svg{
    margin-right: 10px;
  }
`

export const S = {
    StyledWork,
    WorkImg,
    WorkInnerBox,
    WorkTitle,
    WorkText,
    WorkLinkBox,
    WorkLink,
}
