import { createGlobalStyle } from "styled-components";

export const GlobalStyled = createGlobalStyle`

  *,*::before,*::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }


  body {
  margin: 0;
  color: #FFF;
  overflow-x: hidden;
}

  a{
    text-decoration: none;
  }

  ul{
    list-style: none;
  }

  button{
    background-color: none;
    border: none;
  }
`