import React from "react";
import styled from "styled-components";
import {myTheme} from "../../../styles/Theme.styled";
import {Input} from "../../../components/input/Input";

const StyledContact = styled.section`
  background-color: ${myTheme.colors.grey.light};
`

const ContactForm = styled.form`
  max-width: 800px;
  margin: auto;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 30px;
`

const ContactTextArea = styled.textarea`
  width: 100%;
  height: 300px;
  padding-left: 15px;
  padding-top: 15px;
  background-color: rgba(0, 0, 0, 0.15);
  border: 1px solid #D7D7D7;
  outline: none;
  font-size: 18px;
  font-family: ${myTheme.fonts.josefin};
  color: ${myTheme.colors.main};
  resize: none;
`

const ContactSend = styled.button`
  display: block;
  padding: 12px 20px;
  background-color: rgba(0, 0, 0, 0.15);
  color: ${myTheme.colors.main};
  font-family: ${myTheme.fonts.josefin};
  font-size: 20px;
  width: fit-content;
  cursor: pointer;
`
export const S ={
    StyledContact,
    ContactForm,
    ContactTextArea,
    ContactSend
}