import React, {useState} from 'react';
import {S} from "./Works_Styles";
import {Wrapper} from "../../../components/wrapper/Wrapper";
import {Work} from "./work/Work";
import {Title} from "../../../components/title/Title";
import {GridWrapper} from "../../../components/wrapper/GridWrapper";
import {TabStatusType, WorkTabs} from "./workTabs/WorkTabs";
import {AnimatePresence, motion} from "framer-motion";
import work1 from "../../../assets/img/work-1.jpg";
import work2 from "../../../assets/img/work-2.jpg"
import work3 from "../../../assets/img/work-3.jpg"
import work4 from "../../../assets/img/work-4.jpg"
import work5 from "../../../assets/img/work-5.jpg"
import work6 from "../../../assets/img/work-6.jpg"

const TabsItems: Array<{ status: TabStatusType, title: string }> = [
    {
        title: "All",
        status: "all"
    },

    {
        title: "Sites",
        status: "sites"
    },

    {
        title: "React",
        status: "react"
    },
]

const worksData = [
    {
        src: work1,
        title: "Site-Project #1",
        text: "This is sample project description random things are here in description ",
        type: "sites",
        hrefPreview: "test1",
        hrefCode: "test2",
        id: 1
    },
    {
        src: work2,
        title: "Site-Project #2",
        text: "This is sample project description random things  This is sample project lorem ipsum generator for dummy content",
        type: "sites",
        hrefPreview: "test3",
        hrefCode: "test4",
        id: 2
    },
    {
        src: work3,
        title: "React-Project #3",
        text: "This is sample project description random things are here in description This is sample project lorem ipsum generator for dummy content",
        type: "react",
        hrefPreview: "test5",
        hrefCode: "test6",
        id: 3
    },
    {
        src: work4,
        title: "React-Project #4",
        text: "This is sample  are here in description This is sample project lorem ipsum generator for dummy content",
        type: "react",
        hrefPreview: "test7",
        hrefCode: "test8",
        id: 4
    },
    {
        src: work5,
        title: "React-Project #5",
        text: "This is sample project description random things are here in  lorem ipsum generator for dummy content",
        type: "react",
        hrefPreview: "test9",
        hrefCode: "test10",
        id: 5
    },
    {
        src: work6,
        title: "Site-Project #6",
        text: "This is sample project description random things are here in description This is sample project lorem ipsum generator for dummy content",
        type: "sites",
        hrefPreview: "test11",
        hrefCode: "test12",
        id: 6
    },
]


export const Works = () => {

    const [currentFilterStatus, setCurrentFilterStatus] = useState("all");

    let filteredWorks = worksData;

    if (currentFilterStatus === "sites") {
        filteredWorks = worksData.filter(work => work.type === "sites")
    }

    if (currentFilterStatus === "react") {
        filteredWorks = worksData.filter(work => work.type === "react")
    }

    function changeFilterStatus(value: TabStatusType) {
        setCurrentFilterStatus(value)
    }

    return (
        <S.StyledWorks>
            <Wrapper paddingTop={"100px"} paddingBottom={"70px"}>
                <Title text={"My Works"}/>
                <WorkTabs tabsItems={TabsItems}
                          changeFilterStatus={changeFilterStatus}
                          currentFilterStatus={currentFilterStatus}/>
                <GridWrapper padding={"50px 0"} rowGap={"40px"}>

                    <AnimatePresence>
                        {filteredWorks.map((work) => {
                            return (
                                    <Work
                                          src={work.src}
                                          title={work.title}
                                          text={work.text}
                                          hrefPreview={work.hrefPreview}
                                          hrefCode={work.hrefCode}
                                          key={work.id}/>
                            )
                        })}
                    </AnimatePresence>

                </GridWrapper>
            </Wrapper>
        </S.StyledWorks>
    );
};



