import React from "react";
import styled from "styled-components";
import { Icon } from "../icon/Icon";


type LogoPropsType = {
  src ?: any
}

export const Logo = (props: LogoPropsType) => {
  return(
    <StyledLogo href="#">
      <Icon iconId = {"logo"} width={"40"} height={"40"}/>
    </StyledLogo>
  )
}

const StyledLogo = styled.a`
  display: block;
  height: 40px;
  width: 40px;
`