import React from "react";
import styled from "styled-components";
import { myTheme } from "../../styles/Theme.styled";


export const Title = (props: {text: string}) => {
  return(
    <StyledTitle>
      {props.text}
    </StyledTitle>
  )
}

const StyledTitle = styled.h2`
  font-size: 40px;
  font-family: ${myTheme.fonts.poppins};
  color: white;
  text-align: center;
`