import React from "react";
import { S } from "../HeaderMenu_Styles";

const menuItems = ["Home", "Stack", "Portfolio", "Contact"]


export const Menu: React.FC = () => {

  return (
    <S.NavList>
      {menuItems.map((item, index) => {
        return (
          <li key={index}>
            <S.NavLink href={`#${item.toLowerCase()}`}>{item}</S.NavLink>
          </li>
        )
      })}
    </S.NavList>
  )
}